<template>
    <v-container>
        <v-layout row wrap class="white-background" elevation-1>
                <v-flex class="zero-margin" xs3 sm2 md2 lg2 xl1>
                    <v-img right max-height="100px" min-height="100px"  src="https://picsum.photos/300?random"></v-img>
                </v-flex>
                <v-flex class="margin" xs8 sm7 md8 lg8 xl10>
                    <v-flex xs5>
                    <h4 @click="routeSingle()">{{trip.title}}</h4>
                    </v-flex>
                    <v-flex xs5>
                        <p v-if="trip.start_date !== null && trip.end_date !== null">{{trip.start_date.split('T')[0] +"  -  " + trip.end_date.split('T')[0]}}</p>
                    </v-flex>
                    <v-avatar  class="avatar-margin" size="36px" v-for="(n,index) in trip.users" :key="index" :class="'id--' + n.id">
                        <!--<v-avatar  id="avatar" class="avatar-margin" size="40px"/>-->
                        <!--<img src="https://cdn.vuetifyjs.com/images/john.jpg" alt="John">-->
                    </v-avatar>
                </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import store from '../store'
    import router from '../router'
    import axios from 'axios'
    import Vue from 'vue'
    import * as filestack from 'filestack-js'

    export default {
        name: "PastTrip",
        props:{
            trip: Object,
        },
        computed:{
            unique() {
                return store.getters.uniqueUsers
            }
        },
        methods:{
            async getPlace(place, index){
                await axios({
                    method: 'GET',
                    url: '/place',
                    params: {
                        id: place
                    }
                }).then(res => {
                    Vue.set(this.trip.places,index,res.data)
                })
            },

            async getUser(user, index){
                await axios({
                    method: 'GET',
                    url: '/user',
                    params: {
                        id: user
                    }
                }).then(res => {
                    Vue.set(this.trip.users,index,res.data);
                    if(this.unique.includes(res.data.id)) {
                        console.log("Includes");
                    }else {
                        store.commit('changeUniqueUsers', res.data.id);
                        console.log(this.unique)
                    }
                })
            },

            displayAvatar(user, index){
                const apikey = 'AsNx10Lk3SEiGRvMmw223z';
                const client = filestack.init(apikey);

                // let handler = userPath;

                client.retrieve(user.img_path).then((blob) => {
                    let imgLocation = document.getElementsByClassName('id--'+ `${user.id}`);
                    console.log(imgLocation);
                    const urlCreator = window.URL || window.webkitURL;
                    const img = document.createElement('img');
                    img.src = urlCreator.createObjectURL(blob);
                    img.height = 36;
                    img.width = 36;
                    [...imgLocation].forEach((loc,index) => {
                        console.log("attempt");
                        console.log(loc);
                        loc.appendChild(img);
                    })
                }).catch((error) => {
                    console.error(error);
                });
            },

            async routeSingle() {
                await store.commit('changeCurrentlyViewedTrip', this.trip);
                router.push('/itenerary')
            },
        },
        mounted(){
             this.trip.places.forEach((place,index) => {
                if (place.id === undefined) {
                    this.getPlace(place, index);
                }
            });
            this.trip.users.forEach((user,index) => {
                if (user.id === undefined){
                    this.getUser(user, index);
                }
            });
        }
    }
</script>

<style scoped>
   .white-background{
       background-color: white;
    }

    .avatar-margin{
        margin: 2px;

    }

   .zero-margin{
       margin: 0;
   }

    .margin {
        margin-left: 20px !important;
    }
</style>